import { GetAllUsersRoot } from "types/getAllUersRespone";
import { GetAllPhonebookByUserIdRoot } from "types/getAllPhonebookByUserIdResponse";
import client from "./client.services";
import API_URLS from "./endPoints.config";

async function fetchUsers(): Promise<any> {
  // interface will create for response in interface folder
  return client.get(API_URLS.FETCH_USERS);
}

export async function getPhonebookByUserId(
  id: string,
): Promise<GetAllPhonebookByUserIdRoot> {
  const url = `${API_URLS.FETCH_PHONEBOOK}/${id}`;
  return client.get(url);
}

async function fetchPhonebook(id: string): Promise<any> {
  // interface will create for response in interface folder
  const url = `${API_URLS.FETCH_PHONEBOOK}/${id}`;
  return client.get(url);
}

async function addUser(body: any): Promise<any> {
  // interface will create for response in interface folder
  return client.post(API_URLS.ADD_USER, body);
}

async function addPhonebook(body: any): Promise<any> {
  // interface will create for response in interface folder
  return client.post(API_URLS.ADD_PHONEBOOK, body);
}

export async function getAllUsers(): Promise<GetAllUsersRoot> {
  return client.get(API_URLS.FETCH_USERS);
}

export { fetchUsers, addUser, fetchPhonebook, addPhonebook };
