import { ILogin } from "interfaces/auth";
import { GetAllContentRoot } from "types/getAllContentResponse";
import client from "./client.services";
import API_URLS from "./endPoints.config";

async function fetchVideos(): Promise<any> {
  // interface will create for response in interface folder
  return client.get(API_URLS.GET_CONTENT);
}

async function addContent(body: any): Promise<any> {
  // interface will create for response in interface folder
  return client.post(API_URLS.ADD_CONTENT, body);
}
async function deleteContent(id: any): Promise<any> {
  // interface will create for response in interface folder
  return client.delete(`${API_URLS.DELETE_CONTENT}/${id}`);
}

export async function getAllContents(): Promise<GetAllContentRoot> {
  return client.get(API_URLS.GET_CONTENT);
}

export { fetchVideos, addContent, deleteContent };
