/**
 * @format
 */
import { useQuery } from "react-query";
import { fetchVideos, getAllContents } from "services/content.services";
import { fetchUsers } from "services/user.services";
import { QueryKeys } from "utils/QueryKeys";

async function getContent(): Promise<any> {
  try {
    const response: any = await fetchVideos();
    if (response.status === 200) {
      return response;
    }
    return {
      statusCode: 404,
    };
  } catch (error) {
    return Promise.reject(error);
  }
}

const useFetchContent = () => {
  const cacheKey = [QueryKeys.getContent];
  return useQuery(cacheKey, getAllContents, {
    enabled: true,
    refetchOnWindowFocus: false,
  });
};

export { useFetchContent };
